.header {
  height: 30px;
  display: inline-block;
  width: 100%; }

.logo {
  float: left;
  font-weight: 800; }

.links {
  float: right; }

.link {
  float: left;
  margin: 0 15px;
  font-weight: 500;
  opacity: 0.7;
  cursor: pointer;
  line-height: 25px; }
  .link:hover, .link.selectedLink {
    opacity: 1; }
  .link a:focus {
    outline-width: 0; }

@media (max-width: 600px) {
  .links {
    float: left;
    margin: 10px 0 0 -15px;
    display: block; }
  .logo {
    float: none; } }
